// „É¨„Ç§„Ç¢„Ç¶„ÉàÂπÖ
$layout-screen-inner: 1920px;
$layout-width-inner: 1400px;
// „Éñ„É¨„Éº„ÇØ„Éù„Ç§„É≥„Éà
$breakpoints: (
	"sp": "(max-width: 767px)",
	"tab": "(min-width: 768px) and (max-width: #{$layout-width-inner - 1px})",
	//„Çø„Éñ„É¨„ÉÉ„Éà„Çµ„Ç§„Ç∫„Åã„Çâ„Çπ„Éû„Éõ„Åæ„Åß
	"tab-down": "(max-width: #{$layout-width-inner - 1px})",
	//„Çø„Éñ„É¨„ÉÉ„Éà„Çµ„Ç§„Ç∫„Çà„ÇäÂ∞è„Åï„ÅÑ„ÇÇ„ÅÆ„Åô„Åπ„Å¶
	"pc": "(min-width: #{$layout-width-inner}) and (max-width: #{$layout-screen-inner })",
	//„Ç´„É≥„Éó„ÅÆÊåáÂÆö„Çµ„Ç§„Ç∫„Åã„Çâ„Çø„Éñ„É¨„ÉÉ„Éà„Çµ„Ç§„Ç∫„Åæ„Åß
	"pc-up": "(min-width:  #{$layout-width-inner + 1px})",
	//„Ç§„É≥„Éä„Éº„ÅÆÊåáÂÆö„Çµ„Ç§„Ç∫„Çà„ÇäÂ§ß„Åç„ÅÑ
	"sr-up": "(min-width:  #{$layout-screen-inner + 1px})",
	//„Çπ„ÇØ„É™„Éº„É≥„ÅÆÊåáÂÆö„Çµ„Ç§„Ç∫„Çà„ÇäÂ§ß„Åç„ÅÑ
	"retina": "only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx)",
);

// „É°„Éá„Ç£„Ç¢„ÇØ„Ç®„É™„Éº
@mixin mq($breakpoint: sp) {
	@media #{map-get($breakpoints, $breakpoint)} {
		@content;
	}
}

@mixin wp-block-common {

	/*=============== ÂÖ®„Éñ„É≠„ÉÉ„ÇØÂÖ±ÈÄö =================*/

	position: fixed !important;
	top: 0;
	left: 0;
	background: transparent;
	color: #ffffff;
	width: 100vw;
	height: 100vh;

	/* LoadingËÉåÊôØÁîªÈù¢„ÄÄ*/
	#splash {
		/*fixed„ÅßÂÖ®Èù¢„Å´Âõ∫ÂÆö*/
		position: fixed;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh;
		z-index: 1000;
		text-align: center;
		display: none;
		//transition: opacity 1s ease 0.8s;

		&.disappear {
			opacity: 0;
		}
	}

	/* LoadingÁîªÂÉè‰∏≠Â§ÆÈÖçÁΩÆ„ÄÄ*/
	#splash_logo {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		transition: opacity 1s ease 0.8s;

		&.disappear {
			opacity: 0;
		}
	}

	/*ÁîªÈù¢ÈÅ∑Áßª„Ç¢„Éã„É°„Éº„Ç∑„Éß„É≥*/
	.fixbg {
		display: block;
		position: fixed;
		z-index: 990;
		width: 100%;
		height: 100vh;
		top: 0;
		left: 0;
		background-color: #fff;
		transition: opacity 1s ease 0.6s;
		opacity: 1;

		/*„Éï„Çß„Éº„Éâ„Ç¢„Ç¶„Éà*/
		&.disappear {
			opacity: 0;
		}

		/*ÈùûË°®Á§∫*/
		&.hide {
			display: none;
		}
	}

	.splashbg,
	.splashbg2 {
		display: none;

		&.appear {
			display: block;
			position: fixed;
			z-index: 999;
			width: 100%;
			height: 100vh;
			/*‰º∏„Å≥„ÇãËÉåÊôØËâ≤„ÅÆË®≠ÂÆö*/
			animation-duration: 1.2s;
			animation-timing-function: ease-in-out;
			animation-fill-mode: forwards;

			&.virtical_slide {
				top: 0;
				left: 0;
				transform: scaleY(0);
				animation-name: virtical_open;
			}

			&.horizen_slide {
				top: 0;
				left: 0;
				transform: scaleX(0);
				animation-name: horizen_open;
			}

			&.virtical_open {
				animation-name: top_open;
				animation-duration: 1.4s;
				left: 0;
				bottom: 50%;
				transform: scaleY(1);
			}

			&.horizen_open {
				animation-name: left_open;
				animation-duration: 1.8s;
				top: 0;
				right: 50%;
				transform: scaleX(1);
			}
		}
	}

	.splashbg2 {
		&.appear {
			&.virtical_open {
				animation-name: bottom_open;
				animation-duration: 1.4s;
				left: 0;
				top: 50%;
				transform: scaleY(1);
			}

			&.horizen_open {
				animation-name: right_open;
				animation-duration: 1.4s;
				top: 0;
				left: 50%;
				transform: scaleX(1);
			}
		}
	}

	.splashCirclebg {
		display: none;
		position: fixed;
		transform: scale(100);
		/*‰º∏„Å≥„ÇãËÉåÊôØËâ≤„ÅÆË®≠ÂÆö*/
		z-index: 999;
		/*‰∏∏„ÅÆ„Çπ„Çø„Éº„Éà„ÅÆÂΩ¢Áä∂*/
		top: calc(50% - 1rem);
		/*50%„Åã„ÇâÂÜÜ„ÅÆÂçäÂæÑ„ÇíÂºï„ÅÑ„ÅüÂÄ§*/
		left: calc(50% - 1rem);
		/*50%„Åã„ÇâÂÜÜ„ÅÆÂçäÂæÑ„ÇíÂºï„ÅÑ„ÅüÂÄ§*/
		width: 2rem;
		height: 2rem;

		&.appear {
			display: block;
			border-radius: 50%;
			animation-name: scale_down;
			animation-duration: 1s;
			animation-fill-mode: forwards;
		}
	}

	//„ÉÅ„Çß„ÉÉ„ÇØ„Éú„ÉÉ„ÇØ„Çπ
	.is_skip_check {
		position: absolute;
		right: 3em;
		bottom: 3em;
		z-index: 1000;
		width: fit-content;

		@include mq(sp) {
			padding-top: 1em;
			margin-top: 0;
		}

		input[type='checkbox'] {
			display: none;
		}

		label {
			font-size: 1em;
			position: relative;
			display: flex;
			padding: 0.3em 1em;
			align-items: center;
			color: #9e9e9e;
			transition: color 250ms cubic-bezier(.4, .0, .23, 1), background-position 0.3s;
			border-radius: 15px;
			background-image: linear-gradient(to right, rgb(247, 224, 174) 50%, rgb(243, 192, 223) 50%);
			background-position: 0 0;
			background-size: 200% auto;

			@include mq(sp) {
				padding: 1em 0;
			}

			p {
				position: relative;

				@include mq(sp) {
					font-size: 1.4em;
				}

				&:before {
					content: attr(rel);
					position: absolute;
					top: 0;
					left: 0;
					color: #02350f;
					transition: opacity 300ms cubic-bezier(.4, .0, .23, 1);
					opacity: 0;
				}
			}

			span {
				flex: 0 0 auto;
				display: flex;
				justify-content: center;
				align-items: center;
				margin-right: 1em;
				width: 1.2em;
				height: 1.2em;
				background: transparent;
				border: 2px solid #9E9E9E;
				border-radius: 2px;
				position: relative;
				transition: all 250ms cubic-bezier(.4, .0, .23, 1);
				box-sizing: border-box;

				@include mq(sp) {
					width: 0.8em;
					height: 0.8em;
					margin-right: 0.5em;
				}
			}
		}

		label:hover {
			color: #000;
			cursor: pointer;

			span {
				background: rgba(255, 255, 255);
			}
		}

		input[type='checkbox']:checked {
			~p {
				&::before {
					opacity: 1;
				}
			}

			~span {
				border: 0.6em solid #FFEB3B;

				@include mq(sp) {
					border: .4em solid #FFEB3B;
				}

				animation: shrink-bounce 200ms cubic-bezier(.4, .0, .23, 1);

				&:before {
					content: "";
					position: absolute;
					//top: -0.2em;
					//left: -0.2em;
					width: 80%;
					height: 80%;
					border-right: 3px solid transparent;
					border-bottom: 3px solid transparent;
					transform: rotate(45deg);
					transform-origin: 0% 50%;
					animation: checkbox-check 125ms 250ms cubic-bezier(.4, .0, .23, 1) forwards;
				}
			}
		}
	}

	@keyframes scale_down {
		0% {
			/*‰∏∏„ÅÆ„Çπ„Çø„Éº„Éà‰ΩçÁΩÆ„Å®ÂΩ¢Áä∂*/
			transform: scale(100);

		}

		100% {
			/*‰∏∏„ÅÆÁµÇ‰∫Ü‰ΩçÁΩÆ„Å®ÂΩ¢Áä∂*/
			transform: scale(0);
			display: none;
			/*ÁµÇ‰∫ÜÊôÇ„ÅØÊ∂à„Åà„Çã*/
		}
	}


	@keyframes virtical_open {
		0% {
			transform-origin: top;
			transform: scaleY(0);
		}

		50% {
			transform-origin: top;
			transform: scaleY(1);
		}

		50.001% {
			transform-origin: bottom;
		}

		100% {
			transform-origin: bottom;
			transform: scaleY(0);
		}
	}

	@keyframes horizen_open {
		0% {
			transform-origin: right;
			transform: scaleX(0);
		}

		50% {
			transform-origin: right;
			transform: scaleX(1);
		}

		50.001% {
			transform-origin: left;
		}

		100% {
			transform-origin: left;
			transform: scaleX(0);
		}
	}

	@keyframes top_open {
		0% {
			transform-origin: top;
			transform: scaleY(1);
		}

		100% {
			transform-origin: top;
			transform: scaleY(0);
		}
	}

	@keyframes bottom_open {
		0% {
			transform-origin: bottom;
			transform: scaleY(1);
		}

		100% {
			transform-origin: bottom;
			transform: scaleY(0);
		}
	}

	@keyframes left_open {
		0% {
			transform-origin: left;
			transform: scaleX(1);
		}

		100% {
			transform-origin: left;
			transform: scaleX(0);
		}
	}

	@keyframes right_open {
		0% {
			transform-origin: right;
			transform: scaleX(1);
		}

		100% {
			transform-origin: right;
			transform: scaleX(0);
		}
	}

}